.hero_slider {
  padding-top: 5rem;

  .slick-dots {
    background: linear-gradient(135deg, hsl(188, 100%, 15%), #c8e4a7);
    li button {
      font-size: 0;
      line-height: 0;
      display: block;
      width: 20px;
      height: 20px;
      padding: 5px;
      cursor: pointer;
      color: #c8e4a7;
      color: transparent;
      border: 0;
      outline: none;
      // background: linear-gradient(135deg, hsl(188, 100%, 15%), #c8e4a7);
    }
  }
}
