.testimonial__container {
  //   width: 82%;

  .swiper {
    //   width: 82%;
    //   border: 3px solid brown;
    //   .swiper-wrapper {
    //   Bring Swiper Pagination Down
    margin-bottom: 4rem;
    margin-top: 4rem;
    .swiper-slider {
      background: linear-gradient(135deg, hsl(188, 100%, 15%), #c8e4a7);
      padding: 1.5rem 2rem;
      border-radius: 1rem;
      cursor: default;
      font-size: 0.9rem;

      p {
        margin-bottom: 1.5rem;
      }

      .customer {
        display: flex;
        align-items: center;
        gap: 1rem;
        .avatar {
          width: 2.5rem;
          aspect-ratio: 1/1;
          border-radius: 50%;
          overflow: hidden;
        }
      }
    }
    //   }
    // .swiper-pagination-bullet {
    //   // background: $primary-color;
    // }
  }

  /*=================MEDIA QUERIES (SMALL DEVICES)=================*/
  @media screen and (max-width: 600px) {
    .swiper {
      .swiper-wrapper {
        .swiper-slider {
          padding: 1.5rem;
        }
      }
    }
  }
}
