nav {
  background: linear-gradient(135deg, hsl(188, 100%, 15%), #c8e4a7);
  width: 100vw;
  height: 5rem;
  position: fixed;
  top: 0;
  z-index: 10;
  display: grid;
  place-items: center;
  box-shadow: 0 1rem 1rem rgba(black, 0.1);
  z-index: 101;

  // Navbar styles on scroll using JS
  // &.window-scroll {
  //   background: white;
  //   .container {
  //     button {
  //       color: white;
  //     }

  //     a {
  //       color: black;

  //       &.btn {
  //         color: white;
  //       }
  //     }
  //   }
  // }

  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;

    ul {
      display: flex;
      gap: 3rem;
      align-items: center;

      a {
        color: white;
        font-weight: 400;
        font-size: 0.9rem;

        &.btn {
          border-radius: 0.4rem;
          background: #c8e4a7;
          color: hsl(188, 100%, 15%);
        }
      }
    }
    //   Only Needed in Medium and Small Devices
    .hamburger {
      display: none;
    }
  }
}

/*=================MEDIA QUERIES (MEDIUM DEVICES)=================*/
@media screen and (max-width: 1024px) {
  nav {
    // Navbar styles on scroll using JS
    // &.window-scroll {
    //   .container {
    //     a {
    //       color: white;
    //     }
    //   }
    // }
    .container {
      button,
      .hamburger {
        display: inline-block;
        background: transparent;
        color: white;
        font-size: 2rem;
        cursor: pointer;

        // &#close__nav-btn {
        //   display: none;
        // }
      }

      ul {
        position: fixed;
        right: 5%;
        top: 5rem;
        flex-direction: column;
        align-items: flex-start;
        gap: 0;
        display: none;
        perspective: 300px;

        li {
          width: 100%;
          opacity: 0;
          animation: flipNavItem 300ms ease forwards;
          transform-origin: top;

          &:nth-child(2) {
            animation-delay: 200ms;
          }
          &:nth-child(3) {
            animation-delay: 400ms;
          }
          &:nth-child(4) {
            animation-delay: 600ms;
          }
          &:nth-child(5) {
            animation-delay: 800ms;
          }

          @keyframes flipNavItem {
            0% {
              transform: rotateX(90deg);
            }
            100% {
              transform: rotateX(0deg);
              opacity: 1;
            }
          }

          a {
            color: white;
            height: 100%;
            display: block;
            padding: 1.5rem 2rem;
            border-radius: 0;
            background: linear-gradient(
              hsl(188, 100%, 15%),
              darken(hsl(188, 100%, 15%), 12%)
            );
            &.btn {
              border-radius: 0;
              width: auto;
            }
          }
        }
      }
    }
  }
  .nav__logo {
    width: 30%;
    max-height: 5rem;
  }
}
