#services {
  .container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;

    .icon__container {
      width: 10vw;

      svg {
        width: 100%;
        height: 100%;
      }
    }

    article {
      background: linear-gradient(135deg, hsl(188, 100%, 15%), #c8e4a7);
      padding: 1.5rem;
      border-radius: 1rem;
      display: flex;
      gap: 1.5rem;
      height: fit-content;
      transition: all 400ms ease;

      &:hover {
        margin-top: -0.5rem;
      }

      //   i {
      //     font-size: 1.7rem;
      //   }

      div {
        h4 {
          margin-bottom: 1rem;
        }
      }
    }
  }
}

// Add Distance/Gap Between Slides
.slick-slide > div {
  margin: 0 10px;
}
.slick-list {
  margin: 0 -10px;
}

/*=================MEDIA QUERIES (MEDIUM DEVICES)=================*/
@media screen and (max-width: 1024px) {
  #services {
    .container {
      gap: 1rem;

      .icon__container {
        width: 100%;

        svg {
          width: 100%;
          height: 100%;
        }
      }

      article {
        padding: 1.2rem;
        gap: 0.8rem;

        &:hover {
          margin: 0;
        }
      }
    }
  }
}

/*=================MEDIA QUERIES (SMALL DEVICES)=================*/
@media screen and (max-width: 600px) {
  #services {
    .container {
      grid-template-columns: 1fr;

      article {
        div {
          h4 {
            margin-bottom: 0.8rem;
          }
        }
      }
    }
  }
}
