footer {
  background: linear-gradient(
    135deg,
    hsla(0, 0%, 0%, 0.8),
    hsl(188, 100%, 15%),
    hsla(0, 0%, 0%, 0.8)
  );
  color: rgba(white, 0.7);
  padding-top: 6rem;
  margin-top: 6rem;

  .container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 5rem;
    font-size: 0.85rem;

    a {
      color: rgba(white, 0.7);
      transition: all 400ms ease;

      &:hover {
        color: white;
      }
    }

    article {
      display: flex;
      flex-direction: column;
      gap: 1rem;

      & > div {
        display: flex;
        align-items: center;
        gap: 0.6rem;
      }

      & > h3 {
        color: white;
      }
      .footer__socials {
        gap: 1rem;
        font-size: 1.1rem;
      }
    }
  }
  .copyright {
    background: hsla(0, 0%, 0%, 0.65);
    text-align: center;
    padding: 1.5rem 0;
    margin-top: 4rem;
    border-top: 1px solid rgba(white, 0.2);
    font-size: 0.9rem;
  }
}

/*=================MEDIA QUERIES (MEDIUM DEVICES)=================*/
@media screen and (max-width: 1024px) {
  footer {
    .container {
      gap: 2.5rem;
    }
  }
}
/*=================MEDIA QUERIES (SMALL DEVICES)=================*/
@media screen and (max-width: 600px) {
  footer {
    .container {
      grid-template-columns: 1fr;
      text-align: center;

      .btn-primary,
      .footer__socials {
        margin: 0 auto;
      }

      article {
        & > div {
          justify-content: center;
        }
      }
    }
  }
}
