.contact {
  padding-top: 1rem;
}
.contact__container {
  background: hsl(188, 100%, 15%);
  padding: 4rem;
  display: grid;
  grid-template-columns: 40% 60%;
  gap: 4rem;
  height: 30rem;
  margin: 7rem auto;
  border-radius: 1rem;
}
/* ----------Aside------------ */
.contact__aside {
  background: linear-gradient(135deg, hsl(188, 100%, 15%), #c8e4a7);
  padding: 3rem;
  border-radius: 1rem;
  position: relative;
  bottom: 10rem;
}
.aside__image {
  width: 12rem;
  margin-bottom: 2rem;
}
.contact__aside h2 {
  text-align: left;
  margin-bottom: 1rem;
}
.contact__aside p {
  font-size: 0.9rem;
  margin-bottom: 2rem;
}
.contact__details li {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
}
.contact__socials {
  display: flex;
  gap: 0.8rem;
  margin-top: 3rem;
}
.contact__socials a {
  display: flex;
  background: #c8e4a7;
  color: hsl(188, 100%, 15%);
  padding: 0.5rem;
  border-radius: 50%;
  font-size: 0.9rem;
}
.contact__socials a:hover {
  background: hsla(0, 0%, 0%, 0.8);
  color: #c8e4a7;
}
/* ----------Form------------ */
.contact__form {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  margin-right: 4rem;
}
.form__name {
  display: flex;
  gap: 1.2rem;
}
.contact__form input[type="text"] {
  width: 50%;
}
input,
textarea {
  width: 100%;
  padding: 1rem;
  background: hsla(0, 0%, 0%, 0.6);
  color: #c8e4a7;
}
.contact__form .btn {
  background: #c8e4a7;
  color: hsl(188, 100%, 15%);
  width: max-content;
  margin-top: 1rem;
  cursor: pointer;

  &:hover {
    background: hsla(0, 0%, 0%, 0.8);
    color: #c8e4a7;
  }
}
/* ----------MEDIA QUERY FOR TABLETS------------ */
@media screen and (max-width: 1024px) {
  .contact {
    padding: 0;
  }
  .contact__container {
    gap: 1.5rem;
    margin-top: 3rem;
    height: auto;
    padding: 1.5rem;
  }
  .contact__aside {
    width: auto;
    padding: 1.5rem;
    bottom: 0;
  }
  .contact__form {
    align-self: center;
    margin-right: 1.5rem;
  }
}
/* ----------MEDIA QUERY FOR Phones------------ */
@media screen and (max-width: 600px) {
  .contact__container {
    grid-template-columns: 1fr;
    gap: 3rem;
    margin-top: 0;
    padding: 0;
  }
  .contact__form {
    margin: 0 1.5rem 3rem;
  }
  .form__name {
    flex-direction: column;
  }
  .form__name input[type="text"] {
    width: 100%;
  }
}
